@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300&display=swap');

$bg: #1d1d1d;

main {
    height: 100vh;
    width: 100;
    padding-left: 20px;
    overflow-y: auto;

    .top {
        p {
            margin-left: 30px;
        }

        .html,
        .body {
            margin-left: 15px;
        }

        h2 {
            margin-left: 40px;
        }
    }

    .middle {
        margin-left: 15px;
        form {
            margin-left: 35px;
            display: flex;
            flex-direction: column;
            font-family: 'Open Sans';
            overflow-x: hidden;
            
            .user-details {
                display: inline-flex;
                margin-bottom: 10px;
                
                .input-container {
                    margin-right: 40px;
                }

                input {
                    width: 350px;
                    height: 60px;
                }

                label {
                    width: 350px;
                    height: 60px;
                }
            }

            input {
                width: 740px;
                height: 60px;
            }

            label {
                width: 740px;
                height: 60px;
            }

            textarea {
                margin-top: 15px;
                background: transparent;
                border: 2px solid #2D2B39;
                font-family: 'Open Sans';
                width: 740px;
                resize: vertical;
                outline: none;
                padding: 10px;
                min-height: 220px;
                color: #08fdd8;
                font-size: 15px;
            }

            .input-container {
                position: relative;
                overflow: hidden;


                input {
                    background: transparent;
                    color: #08fdd8;
                    outline: none;
                    box-shadow: none;
                    border: none;
                    border-bottom: 2px solid #2D2B39;
                    padding-left: 10px;
                    padding-top: 20px;
                    font-size: 15px;
                }
    
                label {
                    position: absolute;
                    bottom: 0px;
                    left: 0;
                    pointer-events: none;
                }

                label::after {
                    content: '';
                    position: absolute;
                    height: 100%;
                    width: 100%;
                    border-bottom: 3px solid #08fdd8;
                    left: 0px;
                    bottom: -1px;
                    transform: translateX(-100%);
                    transition: transform 0.3s ease;
                }

                span {
                    position: absolute;
                    bottom: 5px;
                    left: 0px;
                    color: #08fdd8;
                    transition: all 0.3s ease;
                }

                input:focus + label span,
                input:valid + label span {
                    transform: translateY(-150%);
                    font-size: 13px;
                }

                input:focus + label::after,
                input:valid + label::after {
                    transform: translateX(0%);
                }
            }

            button {
                margin-top: 30px;
                margin-bottom: 10px;
                background: transparent;
                outline: none;
                box-shadow: none;
                border: 1px solid #08fdd8;
                height: 50px;
                width: 200px;
                transition: 0.5s;
                color: #08fdd8;
                font-family: 'Lexend';
                font-size: 15px;
                letter-spacing: 1px;
            }

            button:hover {
                background: #08fdd8;
                color: black;
                cursor: pointer;
            }
        }

        .result {
            p {
                font-family: 'Open Sans';
                margin-left: 35px;
                color: #08fdd8;
                margin-top: 10px;
            }
        }
    }
}

@media screen and (max-width: 938px) {
    main {
        .middle {
            form {
                .user-details {
                    flex-direction: column;
                }

                textarea,
                input {
                    width: 70vw
                }
            }
        }
    }
}